        @import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

        body {margin: 0; font-family: 'maven pro';}
        .wrapper {max-width: 1440px; margin: auto; position: relative;padding: 40px 25px;}
        .section {position: relative;}
        figure {margin: 0;}
        :root {
            --color-primary: #6B6B3B;
            --color-secondary: #F6EE34;
            --color-tertiary: #D8D4A3;
            --text-header-style: #2c2c2c;
            --text-paragraph-style: #ffffff;
        }
        h1,h2,h3,h4,p{margin: 0;}

        input, textarea, button {box-sizing: border-box;}

        /* hero section */
        #section-hero {min-height: 550px; background: var(--color-primary); color: #fff;margin-bottom: 197px; margin-top: 0;}
        #section-hero .container{}
        #section-hero header {margin-bottom: 87px;justify-content: space-between;align-items: center;display: flex;}
        #section-hero .main-logo{}
        #section-hero nav {}
        #section-hero nav ul {margin: 0;padding: 0;list-style: none;display: flex;justify-content: flex-end;z-index: 1;position: relative;gap: 10px 70px;}
        #section-hero nav ul li {}
        #section-hero nav ul li a {color: #000;text-decoration: none;cursor: pointer;}
        #section-hero nav ul li a:hover,#section-hero nav ul li.active a, #section-hero nav ul li.current-menu-item a {text-decoration: underline;}

        .wp-block-navigation__responsive-container {
            &.is-menu-open {
                background-color: var(--color-tertiary) !important;
                box-shadow: 10px 10px 10px 10px #1a1a1a !important;
            }
        }

        #section-hero .info {width: 43%;}
        #section-hero .info h1 {margin-bottom: 51px;}
        #section-hero .info h1 em {font-style: normal;}
        #section-hero .info h1 .primary {font-size: 75px;}
        #section-hero .info h1 .secondary {display: block; font-size: 50px;}
        #section-hero .info a {display: block; text-decoration: none;font-size: 30px; border-radius: 100px;border: 1px solid var(--color-secondary); color: var(--color-secondary);text-align: center;width: 371px; height: 96px; display: flex; justify-content: center; align-items: center;}
        #section-hero .hero-img {position: absolute;right: 0;top: 0;max-width: 788px;width: 55%;}
        #section-hero .hero-img figure {position: relative;left: 50%;transform: translateX(-50%); display: block;border-radius: 0 0 55px 55px; overflow: hidden; max-width: 769px;}
        #section-hero .hero-img figure img {display: block;}
        #section-hero .wp-block-navigation .wp-block-navigation__submenu-icon {
            color: #000;
        }
        @media (max-width: 1200px) {}
        @media (max-width: 1024px) {
            #section-hero header {margin-bottom: 25px;}
            #section-hero .hero-img {position: static;width: 100%;overflow: unset;margin: auto; max-width: 100%;}
            #section-hero .info {width: 100%;text-align: center;padding-top: 40px;}
            #section-hero .info a {margin: auto;}
            #section-hero {margin-bottom: 0;}
            #section-hero .hero-img figure {border-radius: 20px;}
            #section-hero .hero-img figure img {margin: auto;}
        } 
        @media (max-width: 768px) {
            #section-hero .info h1 .primary {font-size: 50px;}
            #section-hero .info h1 .secondary {font-size: 35px;}
            #section-hero .info h1 {margin-bottom: 33px;}
            #section-hero .info a {font-size: 20px;width: 288px;height: 68px;}
            #section-hero .hero-img figure {border-radius: 20px;position: static;transform: unset;max-width: 100%;}
            #section-hero .hero-img figure img {max-width: 100%;}
        } 
        @media (max-width: 599px) {
            #section-hero nav ul ul, #section-header nav ul ul {
                position: static !important;
                padding-left: 20px;
            }
        }  
        @media (max-width: 480px) {}   /* Phones */